export default {
	data() {
		return {
			form: {
				name: "情人节",
				region: ["120000", "120100", "120103"],
				type: [0, 3],
				delivery: true,
				resource: 0,
				desc: "",
				date: "",
				cover: "",
			},
			rules: {
				name: {
					required: true,
					message: "活动名称不能为空",
				},
				region: {
					required: true,
					message: "活动地区不能为空",
				},
				date: {
					required: true,
					message: "活动时间不能为空",
				},
			},
			options: {
				type: [
					{
						label: "美食/餐厅线上活动",
						value: 0,
					},
					{
						label: "地推活动",
						value: 1,
					},
					{
						label: "线下主题活动",
						value: 2,
					},
					{
						label: "单纯品牌曝光",
						value: 3,
					},
				],
				region: [
					{
						label: "不限",
						value: 0,
					},
					{
						label: "区域一",
						value: 1,
					},
					{
						label: "区域二",
						value: 2,
					},
				],
				resource: [
					{
						label: "线上品牌商赞助",
						value: 0,
					},
					{
						label: "线下场地免费",
						value: 1,
					},
					{
						label: "其他",
						value: 2,
					},
				],
			},
			labelAlign: "right",
			labelWidth: "150rpx",
			isRule: true,
			showMessage: true,
		};
	},

	methods: {
		setLabelAlign(pos) {
			this.labelAlign = pos;
		},
		changeRule() {
			this.isRule = !this.isRule;

			for (let i in this.rules) {
				this.rules[i].required = this.isRule;
			}
		},
		changeShowMessage() {
			this.showMessage = !this.showMessage;
		},
		onSubmit() {
			this.$refs["form"].validate((valid, errors) => {
				if (valid) {
					this.$refs["message"].open({ message: "提交成功" });
					console.log(this.form);
				} else {
					this.$refs["message"].open({
						message: errors[0].message,
						type: "cancel",
					});
				}
			});
		},
		onReset() {
			this.$refs["form"].resetFields();
		},
	},
};
